.Caption{
    background: #48877C;
    padding: 4px 8px 4px 4px;
    cursor: default;
    font-family: Arial Unicode MS, Arial, sans-serif;
    font-weight: bold;
    border-bottom: 1px solid #42655F;
    border-top: 1px solid #91C3BB;
    border-radius: 5px;
    color: white;
}

.DialogBox{
    height: 80%;
}

.Pane{
    padding: 10px;
}

.DialogBoxBottom{
    float: right;
}
.RadioButtonsBox{
    border: 1px solid #6969699c;
    padding: 5px;
}
.TextArea{
    width: 100%;
    height: 95%;
    padding: 4px;
    border: 1px solid #ccc;
    border-top: 1px solid #666;
    font-size: 100%;
    font-family: Arial Unicode MS, Arial, sans-serif;
}